
<template>
	<view>
		<view class="header" :style="{position:headerPosition,top:headerTop}">
			<view class="target" v-for="(target,index) in orderbyList" @tap="select(index)" :key="index" :class="[target.selected?'on':'']">
				{{target.text}}
				<view v-if="target.orderbyicon" class="icon" :class="target.orderbyicon[target.orderby]"></view>
			</view>
		</view> 
		<!-- 占位 -->
		<view class="place"></view>
		<!-- 商品列表 -->
		<view class="goods-list">
			<view class="product-list">
				<view class="product" v-for="(goods) in goodsList" :key="goods.goods_id" @tap="toGoods(goods)">
					<image  :src="goods.logo"></image>
					<view class="name">{{goods.title}}</view>
					<view class="info">
					     <view class="price">{{goods.price}}</view>
						
						<view class="slogan">{{goods.market_price}}</view>
						
					</view>
					<view class="evaluate">
						<view class="evaluate-num">{{goods.comment_num}}条评价</view>
						<view class="evaluate-good">{{goods.good_percent}}%好评</view>
					</view>
				</view>
			</view>
			<view class="loading-text">{{loadingText}}</view>
		</view>
	</view>
</template>

<script>
	
	export default {
		mounted(){
			uni.request({
			url: this.config.url+"goods/lists",
			data: {
				token: this.token
				,cate_id:this.key1,
				sort:1,
				asc:1,
				page:1,
			},
			method: "post",
			success: (res) => {
				console.log(res);
					this.goodsList =res.data.data.data;
				}
			})
		},
		data() {
			return {
				goodsList:[],
				loadingText:"正在加载...",
				headerTop:"0px",
				headerPosition:"fixed",
				orderbyList:[
					{text:"综合",selected:true,orderbyicon:false,orderby:0},
					{text:"销量",selected:false,orderbyicon:['sheng','jiang'],orderby:0},
					{text:"价格",selected:false,orderbyicon:['sheng','jiang'],orderby:0}
				],
				orderby:"sheng",
				id:"",
				key1:"",
				cur_page:0,
				tol_page:1,
			};
		},
		onLoad: function (option) { 
			console.log("option",option)
		    uni.setNavigationBarTitle({
				title: option.title
			});
             this.key1=	option.key;	
			//兼容H5下排序栏位置
			// #ifdef H5
				//定时器方式循环获取高度为止，这么写的原因是onLoad中head未必已经渲染出来。
				let Timer = setInterval(()=>{
					let uniHead = document.getElementsByTagName('uni-page-head');
					if(uniHead.length>0){
						this.headerTop = uniHead[0].offsetHeight+'px';
						clearInterval(Timer);//清除定时器
					}
				},1);
			// #endif
		},
		onPageScroll(e){
			//兼容iOS端下拉时顶部漂移
			if(e.scrollTop>=0){
				this.headerPosition = "fixed";
			}else{
				this.headerPosition = "absolute";
			}
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
		    setTimeout(()=>{
				this.reload();
		        uni.stopPullDownRefresh();
		    }, 1000);
		},
		//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
		onReachBottom(){
			this.cur_page=this.cur_page+1;
			uni.showToast({title: '触发上拉加载'});
			if(this.cur_page>=this.tol_page){
				this.loadingText="到底了";
				return false;
			}else{
				this.loadingText="正在加载...";
			}
		},
		methods:{
			// 排序方式
			sortType(type,asc=1){
				uni.request({
				url: this.config.url+"goods/lists",
				data: {
					token: this.token
					,cate_id:this.key1,
					sort:type,
					asc:asc,
					page:this.cur_page,
				},
				method: "post",
				success: (res) => {
					console.log(res.data.data);
						this.goodsList =res.data.data.data;
						this.cur_page=res.data.data.data.current_page;
						this.tol_page=res.data.data.data.last_page;
					}
				})
			},
			reload(){
				console.log("reload");
				this.sortType(1);
				
			},
			//商品跳转
			toGoods(e){
				console.log(e)
				var id=e.id;
				uni.navigateTo({
					url: "/pages/goods/goods?id="+id
				})
			},
			//排序类型
			select(index){
				
				let tmpTis = this.orderbyList[index].text+"排序 "
				if(this.orderbyList[index].orderbyicon){
					let type = this.orderbyList[index].orderby==0?'升序':'降序';
					if(this.orderbyList[index].selected){
						type = this.orderbyList[index].orderby==0?'降序':'升序';
						this.orderbyList[index].orderby = this.orderbyList[index].orderby==0?1:0;
					}
					tmpTis+=type
				}
				this.orderbyList[index].selected = true;
				let len = this.orderbyList.length;
				for(let i=0;i<len;i++){
					if(i!=index){
						this.orderbyList[i].selected = false;
					}
				}
				uni.showToast({title:tmpTis,icon:"none"});
				switch(index){
					case 0:
					this.sortType(1);
					break;
					case 1:
					if(this.orderbyList[index].orderby){
						this.sortType(2,2);
					}else{
						this.sortType(2);
					}
					break;
					case 2:
					if(this.orderbyList[index].orderby){
						this.sortType(3,2);
					}else{
						this.sortType(3);
					}
					break;
				}
			}
		}
		
	}
</script>

<style lang="scss">
	@font-face {font-family:"HMfont-home";src:url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');}
	.icon {
		font-family:"HMfont-home" !important;
		font-size:26upx;
		font-style:normal;
		&.sheng {
			&:before{content:"\e737";}
		}
		&.jiang {
			&:before{content:"\e736";}
		}
		
	}
	.header{
		width: 92%;
		padding: 0 4%;
		height: 79upx;
		display: flex;
		justify-content: space-around;
		align-items: flex-end;
		position: fixed;
		top: 0;
		z-index: 10;
		background-color: #fff;
		border-bottom: solid 1upx #eee;
		.target{
			width: 20%;
			height: 60upx;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 28upx;
			margin-bottom: -2upx;
			color: #aaa;
			&.on{
				color: #555;
				border-bottom: 4upx solid #f06c7a;
				font-weight: 600;
				font-size: 30upx;
			}
			
			
		}
		
	}
.place{
		
		background-color: #ffffff;
		height: 100upx;

	}
.goods-list{
		.loading-text{
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			height: 60upx;
			color: #979797;
			font-size: 24upx;
		}
		.product-list{
			width: 92%;
			padding: 0 4% 3vw 4%; 
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			.product{
				width: 48%;
				border-radius: 20upx;
				background-color: #fff;
				margin-bottom:30upx;
				box-shadow: 0upx 5upx 25upx rgba(0,0,0,0.1);
				display:flex;
				flex-direction:column;
				
				image{
					width: 100%;
					height:273upx;
					border-radius: 20upx 20upx 0 0;
					margin-bottom:30upx;
				}
				.name{
					width: 92%;
					padding: 10upx 4%;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					text-align: justify;
					overflow: hidden;
					font-size: 30upx;
				}
				.info{
					display: flex;
					justify-content: flex-start;
					align-items: center;
					width: 92%;
					padding: 10upx 4% 10upx 4%;
					
					.price{
						color: #e65339;
						font-size: 30upx;
						font-weight: 600;
					}
					.slogan{
						font-size:24upx;
font-family:PingFang-SC-Regular;
font-weight:400;
text-decoration:line-through;
color:rgba(153,153,153,1);
margin-left:20upx;
					}
				}
				.evaluate{
					width:92%;
					padding:0 4% 20upx;
					display:flex;
					justify-content: flex-start;
					align-items: center;
					color:rgba(102,102,102,1);
                     font-size: 22upx;
					 .evaluate-good{
						 margin-left:20upx;
					 }
				}
			}
			
		}
	}
</style>
